import React from 'react'
import CountUp from 'react-countup';
import { useNavigate } from 'react-router-dom';
import { Tooltip as ReactTooltip } from 'react-tooltip'

const CountCard = (props) => {

  // =======NAVIGATION CONSTANST==========
  const navigate = useNavigate()

  // =============VIEW BUTTON ACTION===============
  const actionFun = () => {
    props?.location == 'property' && navigate("/propertyDashboard")
    props?.location == 'trade' && navigate('/view-trade')
    props?.location == 'water' && navigate('/view-water')
  }

  // ===========restrict component when there is no data============
  if(props?.data?.length == undefined || props?.data?.length == 0){
    return
  }


  // ==============CSS STYLE CONSTANT==============
  let buttonStyle = 'z-10 px-4 py-1 w-max text-xs 2xl:text-sm rounded-md hover:shadow-md bg-indigo-500 hover:bg-indigo-600 text-white cursor-pointer poppins'

  return (
    <>
    
    {/* ===========MAIN============== */}
    <div className={`cursor-pointer flex-shrink px-4 max-w-full w-[25rem] md:px-3 2xl:px-6 scale-90`} onClick={() => actionFun()} style={{zIndex : 500}}>
         
         <div className={` px-2 py-4 2xl:px-3 2xl:py-6 2xl:mb-4 mb-2 flex items-center gap-x-4 shadow-lg rounded-lg border-2 border-indigo-300 hover:border-indigo-500 hover:bg-indigo-50 transform transition duration-300 ease-in-out hover:-translate-y-2 hover:shadow-xl`}>
           
           {/* ============COUNTING============== */}
           <div className="inline-block font-bold poppins 2xl:text-4xl text-2xl text-indigo-600 mb-1 bg-white p-4 shadow-md rounded-lg">
           <CountUp className='font-semibold poppins' end={props?.data?.length == undefined ? 0 : props?.data?.length}/>
             </div>

              <div className='flex flex-col items-center'>
                {/* ========LABEL========== */}
                <h3 className="2xl:text-lg text-sm leading-normal font-semibold text-gray-800 poppins">Total Available {props?.label}</h3>
                <h4 className='text-xs 2xl:text-sm mb-2'>Total available <span className="lowercase">{props?.label} at all ulbs</span></h4>

                  {/* =========BUTTON========== */}
                  <button className={buttonStyle} onClick={() => actionFun()} id={props?.label} data-tooltip-content={`Click to view your ${props?.label} list`}>View</button>

              </div>
             
             </div>
           
         </div>

         {/* ============TOOLTIP================== */}
         <ReactTooltip anchorId={props?.label} place="bottom" type="info" effect="float" className='poppins text-xs bg-indigo-600 text-white' style={{zIndex : 999}} />
    </>
  )
}

export default CountCard

