import React, { useState } from 'react'
import '../assets/Fonts.css'
import home from '../../../../Components/Media/home.png'
import water2 from '../../../../Components/Media/water2.png'
import ac4 from '../../../../Components/Media/ac4.png'
import team from '../../../../Components/Media/team.png'
import ModuleCard from './ModuleCard'
import ModuleCard2 from './ModuleCard2'

const Modules = () => {

  const [toggle, settoggle] = useState(false)

  return (
    <>

      <div className='flex flex-col md:flex-row flex-wrap gap-y-4 justify-evenly items-center '>
        <ModuleCard image={home} label={'Property'} apply={'/safform/new/0'} />
        <ModuleCard image={water2} label={'Water'} apply={'/water-apply'} />
        <ModuleCard image={team} label={'Trade'} apply={'/trade-new-apply'} />

        <span className='self-end sm:mr-0 mr-2 cursor-pointer hover:underline text-xs ml-4 2xl:ml-0 2xl:text-sm select-none font-semibold' onClick={() => settoggle(!toggle)}>{toggle ? '<< Less' : 'More >>'}</span>
        {toggle && <ModuleCard image={ac4} label={'Advertisement'} apply={'/advertisement-dashboard'} />}
      </div>

    </>
  )
}

export default Modules

