import React from 'react'
import { useNavigate } from 'react-router-dom'
import { Tooltip as ReactTooltip } from 'react-tooltip'

const ModuleCard = (props) => {

  // =======NAVIGATE CONSTANT==========
  const navigate = useNavigate()

  // ========TRACK BUTTON ACTION==============
  const actionFun = () => {
    props?.label == 'Property' && navigate('/propertyDashboard')
    props?.label == 'Water' && navigate('/track-water')
    props?.label == 'Trade' && navigate('/track-trade')
    props?.label == 'Advertisement' && navigate('/advertisement-dashboard')
  }

  // ===========CARD CLICK ACTION===============
  const cardAction = () => {
    props?.label == 'Property' && navigate('/propertyDashboard')
    props?.label == 'Water' && navigate('/view-water')
    props?.label == 'Trade' && navigate('/view-trade')
    props?.label == 'Advertisement' && navigate('/advertisement-dashboard')
  }


  // =============CSS STYLE VARIABLES==============
  let cardStyle = 'md:w-max w-full rounded-tl-md rounded-br-md hover:scale-100 transition-all duration-300 scale-90  shadow-md text-sm bg-indigo-50 flex flex-row hover:shadow-lg shadow-indigo-200 hover:shadow-indigo-200 sm:-mr-10 animate__animated animate__fadeIn animate__faster subpixel-antialiased'

  let buttonStyle = 'z-[999] px-2 py-1 text-xs 2xl:text-sm rounded-md hover:shadow-md bg-indigo-500 hover:bg-indigo-600 text-white cursor-pointer poppins'

  return (
    <>
    
    {/* ========MAIN=========== */}
    <div className={cardStyle}>

                {/* =========IMAGE LEFT PART========= */}
                <img className='w-[5rem] 2xl:w-[6rem] rounded-r-full p-1 py-2 2xl:py-4 2xl:p-2 bg-indigo-300 cursor-pointer ' src={props?.image} onClick={() => cardAction()}/>
                
                {/* ============CONTENT RIGHT PART============== */}
                <div className='px-2 2xl:px-4 py-1 2xl:py-2 flex flex-col gap-2 2xl:gap-4 justify-center items-center'>

                  {/* ===========CARD LABEL============== */}
                    <div className=' poppins cursor-pointer ' onClick={() => cardAction()}>
                      <div className='text-md 2xl:text-base font-semibold'>Track Your {props?.label}</div>
                      <div className='text-xs'>Access all your <span className="lowercase">{props?.label}</span> related issues <br />and get all your work done.</div>
                    </div>

                    {/* =========BUTTONS============== */}
                    <div className='flex flex-row gap-x-2 2xl:gap-x-4 -ml-10'>

                      {/* ==========APPLY BUTTON=========== */}
                        <button className={buttonStyle} 
                        onClick={() => navigate(`${props?.apply}`)}
                        id={`${props?.label}/1`} data-tooltip-content={`Click to apply new ${props?.label}`}
                        >Apply</button>

                        {/* ============TRACK BUTTON=========== */}
                        <button className={buttonStyle} onClick={() => actionFun()} 
                        id={`${props?.label}/2`} data-tooltip-content={`Click to view your pending ${props?.label} application list`}
                        >Track</button>
                    </div>
                </div>
            </div>

            {/* ===========TOOLTIP============== */}
            <ReactTooltip anchorId={`${props?.label}/1`} place="bottom" type="info" effect="float" className='poppins text-xs bg-indigo-600 text-white' style={{zIndex: 999}} />
            <ReactTooltip anchorId={`${props?.label}/2`} place="bottom" type="info" effect="float" className='poppins text-xs bg-indigo-600 text-white' style={{zIndex: 999}} />
    
    </>
  )
}

export default ModuleCard

// export to: Module